import React from "react";
import UI from '../../Utils/MaterialUI';
import { makeStyles } from "@material-ui/core/styles";
import { useDispatch, useSelector } from "react-redux";
import { AlertTitle } from '@material-ui/lab';
import { alert } from '../../Component/Alert/AlertAction';


function Alert(props) {
    return <UI.MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        "& > * + *": {
            marginTop: theme.spacing(2),
        },
    },
}));

const AlertMessages = (props) => {
    const alertState = useSelector((state) => state.AlertReducer);
    const dispatch = useDispatch();
    const classes = useStyles();

    const handleClose = (event, reason) => {
        dispatch(alert({
            status: false,
            type: "",
            message: ""
        }))
    };

    return (
        <div className={classes.root}>
            {
                alertState.status &&
               
                    <UI.Snackbar open={alertState.status} autoHideDuration={3000} onClose={handleClose} className="alert-message">
                        <Alert onClose={handleClose} severity={alertState.type}>
                            <AlertTitle>{(alertState.type).charAt(0).toUpperCase() + (alertState.type).slice(1)}</AlertTitle>
                            {alertState.message}
                        </Alert>
                    </UI.Snackbar>

            }


        </div>
    );
};

export default AlertMessages;
