import React from 'react';
import M from '../Utils/MaterialUI';

const control = {
    textField: (props) => {
        // console.log('Form Component',props);
        const { input, custom, label, meta} = props;
        const { touched, error, dirty, pristine, visited } = meta;
        const { onChange ,value} = input;
        return (
            <>
                <M.TextField
                    {...input}
                    {...custom}
                    label={label}
                    style={{
                        width: '50%',
                        color: '#B1B1B1',
                        fontSize: '16px',
                        opacity: 1
                    }}
                    required
                    value = {value}
                />
                <span style={{ color: 'red' }}>{((touched || visited) && error) ? error : ''}</span>
            </>

        )
    },
    textArea: (props) => {
        const { input, type, label, custom, meta } = props;
        const { touched, error } = meta
        return (
            <>
                <M.TextField
                    {...input}
                    {...custom}
                    multiline
                    rows={4}
                    label={label}
                    style={{
                        width: '50%',
                        color: '#B1B1B1',
                        fontSize: '16px',
                        opacity: 1
                    }}
                    required
                />
                <span style={{ color: 'red' }}>{(touched && error) ? error : ''}</span>
            </>
        )
    }

}

export default control;
