import Constant from "../../Utils/Constant";

const initialState = {
    isLogin: false,
    userData: {},
    userToken:"",
};

export default function LoginReducer(state = initialState, action) {
    switch (action.type) {
        case Constant.reducerActions.IS_USER_LOGIN:
            return ({ ...state, isLogin: action.payload })
        case Constant.reducerActions.SAVE_USER_DATA:
            return ({ ...state, userData: action.payload })
            case Constant.reducerActions.SAVE_USER_TOKEN:
                return ({ ...state, userToken: action.payload })
    }
    return state;

} 