import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import './ProductCard.scss';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import IconButton from '@material-ui/core/IconButton';
import StarIcon from '@material-ui/icons/StarRate';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Constant from '../../../../Utils/Constant'
import Button from '@material-ui/core/Button';

const options = [
    'Edit',
    'Delete'
];

const ITEM_HEIGHT = 45;
const useStyles = makeStyles((theme) => ({
    root: {
        width: '240px',
        height: '168px'
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    },
    small: {
        width: theme.spacing(3),
        height: theme.spacing(3),
    }
}));

const ProductCard = (props) => {
    const { onClick} = props;
    const classes = useStyles();

    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
        console.log('handleClick', event.currentTarget);
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    return (
        <>
            <div className='product-card'>
                <div className="content" >
                    <Grid
                        container
                        direction='row'
                        style={{ flex: 1, display: 'flex', width: '100%', height: '100%' }}                    >

                        <Grid style={{ flex: 1, display: 'flex', }}>
                            <Grid item md={4}>
                                <img style={{ width: 130, height: 130 }} src={Constant.assets.ic_product} />
                            </Grid>
                        </Grid>

                        <Grid style={{ flex: 1 }}>
                            <Grid item md={4} style={{ alignItems: 'flex-end', position: 'relative', right: -120, top: -15 }}>
                                <Grid
                                    container
                                    direction="row"
                                    justify="flex-end"
                                    alignItems="center"
                                >

                                    <IconButton
                                        aria-label="more"
                                        aria-controls="long-menu"
                                        aria-haspopup="true"
                                        onClick={handleClick}
                                    >
                                        <MoreVertIcon className='morevert' />
                                    </IconButton>
                                    <Menu
                                        id="long-menu"
                                        anchorEl={anchorEl}
                                        keepMounted
                                        open={open}
                                        onClose={handleClose}
                                        PaperProps={{
                                            style: {
                                                maxHeight: ITEM_HEIGHT * 4.5,
                                                width: '10ch',
                                            },
                                        }}
                                    >
                                        {options.map((option) => (
                                            <MenuItem key={option} selected={option === 'Pyxis'} onClick={handleClose}>
                                                {option}
                                            </MenuItem>
                                        ))}
                                    </Menu>
                                </Grid>
                            </Grid>

                            <Grid direction='column' style={{ flex: 1, display: 'flex', marginTop: -30 }}>
                                <div>
                                    <div className='product-name'>{'Martha Vendez'}</div>
                                    <div style={{ flexDirection: 'row', flex: 1, display: 'flex', marginTop: 5, }}>
                                        <div className="category">Category -</div> &nbsp;
                                        <div className='category-name'>{'Medical'}</div>
                                    </div>
                                    <Button                                    
                                        variant="outlined"
                                        color="primary"
                                        onClick={onClick}
                                        className='preview' >
                                        Preview
                                </Button>
                                    <div style={{ flexDirection: 'row', flex: 1, display: 'flex', marginTop: 5, alignItems: 'center' }}>
                                        <img
                                            style={{ width: 25, height: 25, borderRadius: 25/2 }}
                                            src={Constant.assets.ic_userImage} />
                                        <div className="author">{'Monica sharma'}</div>
                                        <div style={{flex:1, display:'flex', alignItems:'center', justifyContent:'flex-end' }}>
                                            <StarIcon style={{ color: ' #FFD800', width: 20, height: 20, opacity: 1, }} />
                                            <div className="author">{'4.5'}</div>
                                        </div>
                                    </div>
                                </div>
                            </Grid>
                        </Grid>
                    </Grid>
                </div>
            </div>
        </>
    );
}

export default ProductCard;
