export function validateInput(userEmail,userPassword) {
    var mailformat = /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,15}/;

    if(!userEmail && !userPassword){
       return 'EMPTY_ERROR'
   }else{
    if(!userEmail)
    return 'EMAIL_ERROR';

    if(userEmail){
        if(!userEmail.match(mailformat)){
            return 'INVALID_EMAIL'
        }
    }



    if(!userPassword)
    return 'PASS_ERROR'
    
    if(userEmail && userPassword){
        return 'VALIDATE'
    }
   }
   
   
   }
