import { combineReducers } from 'redux';
import loginReducer from '../Pages/Login/LoginReducer';
import SuperAdminReducer from '../Pages/Dashboard/SuperAdmin/SuperAdminReducer'
import AlertReducer from '../Component/Alert/AlertReducer';
import LoaderReducer from '../Component/Loader/LoaderReducer';
import { reducer as formReducer } from 'redux-form'


export default combineReducers({
    loginReducer: loginReducer,
    SuperAdminReducer: SuperAdminReducer,
    AlertReducer: AlertReducer,
    LoaderReducer: LoaderReducer,
    form: formReducer
})

