import React, { useState, useEffect } from 'react';
import CompanyCard from '../../../../Component/Company/CompanyCard';
import Grid from '@material-ui/core/Grid';
import CustomAddButton from '../../../../Component/CustomAddButton';
import { makeStyles } from '@material-ui/core/styles';
import request from '../../../../Server/api';
import Constant from '../../../../Utils/Constant';
import { alert } from '../../../../Component/Alert/AlertAction';
import { loader } from '../../../../Component/Loader/LoaderAction';
import { useDispatch } from "react-redux";
import { dashboard } from '../SuperAdminAction';
import RecordNotFound from '../../../../Component/RecordNotFound';
import ProductCard from './ProductCard'


const useStyles = makeStyles({
  loadMore: {
    color: '#11CDEF',
    opacity: '1',
    fontSize: '14px',
    marginTop: '3%',
    marginBottom: '3%',
    cursor: 'pointer'
  },
  content: {
    // overflowX : 'scroll'
    // backgroundColor : '#f1f3f9',
    // paddingBottom : '3%'
  }
})

export default function Product(props) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [data, setData] = useState({
    list: [],
    isData: false
  })


  useEffect(() => {
    loadCompany()
  }, [])

  const loadCompany = () => {
    dispatch(loader(true));
    request('COMPANY_LIST').then((result) => {
      let responce = result.data;
      if (responce.isSuccess) {
        setData({
          ...data, list: responce.data,
          isData: true
        })
      }
      dispatch(loader(false));

    }).catch(err => {
      dispatch(loader(false));
      if (err.response) {
        let error = err.response.data;
        dispatch(alert({
          status: true,
          message: error.message,
          type: 'error'
        }))
      }
    })
  }

  const onClick = () => {  
    props.history.push('/superAdmin/product-detail')
  }

  const loadMore = async () => {
    let newData = [
      {
        name: 'Getsocio',
        email: 'Info@getsocio.com',
        desc: '17-July-20',
      },
      {
        name: 'Getsocio',
        email: 'Info@getsocio.com',
        desc: '17-July-20',
      },
      {
        name: 'Getsocio',
        email: 'Info@getsocio.com',
        desc: '17-July-20',
      },
      {
        name: 'Getsocio',
        email: 'Info@getsocio.com',
        desc: '17-July-20',
      }
    ]

    let temp = data.list.concat(newData)
    setData({ ...data, list: temp });
  }
  

  return (
    <>

      <div className={classes.content}>
        <Grid
          container
          direction="row"
          marginBottom={10}
          justify="flex-end"
          alignItems="center"
        >
          {/* <CustomAddButton onClick={onClick.bind(this)} /> */}
        </Grid>

        {
          (data.list.length < 1 && data.isData) ? (<RecordNotFound />) : ''
        }

        {(data.list.length > 0) && <>
          <Grid container spacing={2}>
            {data.list.map((row, index) => (
              <Grid item md={4} key={row._id}>
                <ProductCard data={row}  onClick={onClick.bind(this)}  />
              </Grid>
            ))}
          </Grid>
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center">
            <span className={classes.loadMore} onClick={loadMore}> Load More({data.list.length})</span>
          </Grid>
        </>
        }
      </div>
    </>
  );
}
