import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import './CompanyCard.scss';
import company from '../../assets/images/company.png';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import { useHistory } from 'react-router-dom';

const options = [
  'Edit',
  'Suspend'
];

const ITEM_HEIGHT = 45;
const useStyles = makeStyles({
  root: {
    width: '240px',
    height: '168px'
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  }
});




const CompanyCard = (props) => {
  // console.log('Company card props',props);
  
  const history = useHistory();
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (event, value, id) => {
    if (value == 'Edit') {
      history.push(`/superAdmin/add-company/${id}`)
    } else if (value == 'Suspend') {
      props.handleMenuClick(value, id);
    }
    setAnchorEl(null);
  };

  return (
    <>
      <div className={`company-card ${props.backColor}`}>
        <div className="content">
          <Grid
            container
          >
            <Grid item md={4}></Grid>
            <Grid item md={4}>
              <img src={(props.data.logo) ? props?.data?.logo : company} />
            </Grid>
            <Grid item md={4}>
              <Grid
                container
                direction="row"
                justify="flex-end"
                alignItems="center"
              >

                {
                  (props.backColor =='active') ? (
                    <IconButton
                  aria-label="more"
                  aria-controls="long-menu"
                  aria-haspopup="true"
                  onClick={handleClick}
                >
                  <MoreVertIcon className='handCursor' />
                </IconButton>
                  ) : ''
                }

                <Menu
                  id="long-menu"
                  anchorEl={anchorEl}
                  keepMounted
                  open={open}
                  onClose={handleClose}
                  PaperProps={{
                    style: {
                      maxHeight: ITEM_HEIGHT * 4.5,
                      width: '15ch',
                    },
                  }}
                >
                  {options.map((option) => (
                    <MenuItem key={option} selected={option === 'Pyxis'} onClick={(e) => handleClose(e, option, props?.data?._id)}>
                      {option}
                    </MenuItem>
                  ))}
                </Menu>
              </Grid>
            </Grid>
          </Grid>

        </div>
        <div>
          <div className='label'>{props?.data?.name}</div>
          <div className="email">{props?.data?.email}</div>
          <div className="date">{'31-07-2020'} 
          </div>
        </div>
      </div>
    </>
  );
}

export default CompanyCard;
