import UI from '../../../../Utils/MaterialUI';

import React, { useState, useEffect } from 'react';
import Constant from '../../../../Utils/Constant';
import CustomCard from '../../../../Component/CustomCard';
import { alert } from '../../../../Component/Alert/AlertAction';
import { loader } from '../../../../Component/Loader/LoaderAction';
import { useDispatch, useSelector } from "react-redux";
import request from '../../../../Server/api';
import { Field, reduxForm, formValueSelector } from 'redux-form';
import { useHistory, useParams } from 'react-router-dom';
import validate from '../../../../Utils/Validation';
import control from '../../../../Component/FormComponent';
import axios from 'axios';
import BottomNavigation from '@material-ui/core/BottomNavigation';


const onSubmit = (values) => {
    console.log(values);
}

const useStyles = UI.makeStyles({
    card: {
        background: 'white',
        height: '90%',
        width: '100%',
        borderRadius: 20,
        alignContent: '',
        alignSelf: ''
    },

    imageUpload: {
        width: '99px',
        height: '99px',
        boxShadow: '0px 3px 6px #00000029',
        opacity: '1',
        backgroundColor: '#11CDEF',
        color: '#FFFFFF',
        borderRadius: '100%',
        cursor: 'pointer',
        textAlign: "center",
    },
    companyLogo: {
        width: '99px',
        height: '99px',
        borderRadius: '100%',
    },
    defaultLogo: {
        paddingTop: '24%'
    },
    inputLabel: {
        color: '#B1B1B1'
    },
    container: {
        padding: '2% 1% 1% 2%'
    },

    rowMargin: {
        marginBottom: '1%'
    },
    btnSubmit: {
        width: '20%',
        background: Constant.colors.btnColor,
        color: '#FFFFFF'
    },
    footer: {
        // position: 'fixed',
        // left: 0,
        // bottom: 0,
        // marginTop: '15%',
        marginBottom: '-50px',
        zIndex: 1,
        width: '100 %',
        backgroundColor: 'red',
        color: 'white',
        textAlign: 'center',
        display: "flex"
        // height:'50vh'
    }
});

let AddCompany = (props) => {
    const { handleSubmit, handleChange, pristine, reset, submitting, invalid, value } = props
    const { companyId } = useParams();
    const classes = useStyles();
    const dispatch = useDispatch();
    const history = useHistory()
    const [data, setData] = useState({
        logo: '',
        name: '',
        email: '',
        desc: '',
        userTypeId: '5f2927b16cbcf64be4fe2fae'
    })

    useEffect(() => {
        if (companyId) {
            loadCompany()
        }

    }, [])


    const loadCompany = () => {
        props.dispatch(loader(true))
        request('COMPANY_BY_ID', null, {
            companyId: companyId
        }).then((result) => {
            let responce = result.data;
            props.dispatch(loader(false))
            props.initialize(responce.data)
            setData(responce.data)
        }).catch(err => {
            if (err.response) {
                let error = err.response.data;
                props.dispatch(alert({
                    status: true,
                    message: error.message,
                    type: 'error'
                }))
                props.dispatch(loader(false))
            }
        })
    }

    const selectFile = (event) => {
        document.getElementById('fileId').click();
    }

    const changeFile = (event) => {
        getBase64(event.target.files[0], (base64Image) => {
            setData({ ...data, companyLogo: base64Image })
        })
    }

    const getBase64 = (file, callback) => {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function () {
            callback(reader.result)
        };
        reader.onerror = function (error) {
            console.log('Error: ', error);
        };
    }

    const onSubmit = (formData) => {
        dispatch(loader(true))
        if (companyId) {
            let params = {
                companyId: companyId
            }

            delete formData._id
            request('COMPANY_UPDATE', formData, params).then((result) => {
                let responce = result.data;
                dispatch(alert({
                    status: true,
                    message: responce.message,
                    type: 'success'
                }))
                setData({
                    ...data,
                    logo: '',
                    name: '',
                    email: '',
                    desc: ''
                })
                dispatch(loader(false))
                history.push('/superAdmin/home');
            }).catch(err => {
                if (err.response) {
                    let error = err.response.data;
                    dispatch(alert({
                        status: true,
                        message: error.message,
                        type: 'error'
                    }))
                    dispatch(loader(false))
                }
            })

        } else {
            formData['userTypeId'] = '5f2927b16cbcf64be4fe2fae';
            request('COMPANY_ADD', formData).then((result) => {
                let reaponce = result.data;
                dispatch(alert({
                    status: true,
                    message: reaponce.message,
                    type: 'success'
                }))
                setData({
                    ...data,
                    logo: '',
                    name: '',
                    email: '',
                    desc: ''
                })
                dispatch(loader(false))
                history.push('/superAdmin/home');
            }).catch(err => {
                if (err.response) {
                    let error = err.response.data;
                    dispatch(alert({
                        status: true,
                        message: error.message,
                        type: 'error'
                    }))
                    dispatch(loader(false))
                }
            })
        }
    }

    return (
        <>
            <form onSubmit={handleSubmit(props => onSubmit(props))}>
                <CustomCard card={classes.card} children={
                    <div className={classes.container}>
                        <UI.Grid
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="flex-start"
                        >
                            <div className={classes.imageUpload}>
                                {
                                    data.companyLogo ? (<img src={data.companyLogo} onClick={selectFile} className={classes.companyLogo} />) : (<img src={Constant.assets.addIcon} onClick={selectFile} className={classes.defaultLogo} />)
                                }
                                <input type="file" id="fileId" name="companyLogo" hidden onChange={changeFile} />
                            </div>
                        </UI.Grid>
                        <UI.Grid
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="flex-start"
                            className={classes.rowMargin}
                        >
                            <Field name="name" component={control.textField} type="text" label={Constant.strings.lbl_company} onChange={handleChange} />

                        </UI.Grid>
                        <UI.Grid
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="flex-start"
                            className={classes.rowMargin}
                        >
                            <Field name="email" component={control.textField} type="text" label={Constant.strings.lbl_company_email} />
                        </UI.Grid>

                        <UI.Grid
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="flex-start"
                            className={classes.rowMargin}
                        >

                            <Field name="desc" component={control.textArea} type="text" label="Description" />
                        </UI.Grid>

                        <UI.Grid
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="flex-start"
                            className={classes.rowMargin}
                        >
                            <UI.Button className={classes.btnSubmit} variant="contained"
                                style={{ backgroundColor: Constant.colors.btnColor }} type="submit" disabled={invalid || pristine || submitting}>
                                {companyId ? 'Update' : 'Submit'}
                            </UI.Button>&nbsp;&nbsp;
                            <UI.Button variant="contained" color="secondary" onClick={(e) => {
                                history.push('/superAdmin/home')
                            }}>Cancel</UI.Button>
                        </UI.Grid>
                    </div>
                }
                />
                <BottomNavigation className={classes.footer}></BottomNavigation>
            </form>
        </>
    )
}
AddCompany = reduxForm({
    form: 'AddCompany',
    onSubmit,
    validate,
    fields: ['name', 'email', 'desc'],
    initialValues: {}
})(AddCompany)

export default AddCompany;
