import React from 'react';
import Constant from '../Utils/Constant'
import { Paper, Grid, TextField, FormControlLabel, Checkbox, Button, Typography } from '@material-ui/core';
import { CircularProgress } from '@material-ui/core';

export default function CustomButton(props) {
    return (
        <Grid container spacing={8} >
            <Grid item md={true} sm={true} xs={true}>
                <Button fullWidth onClick={props.onClick} variant="contained" color={Constant.colors.btnColor}
                    style={{
                        textTransform: "none", color: 'white', background: Constant.colors.btnColor, width: '100%', height: 50
                    }}>{props.title}
                    {
                        props.isLoading && (
                            <CircularProgress />

                        )
                    }

                </Button>
            </Grid>
        </Grid>
    );

}