import UI from '../../../../Utils/MaterialUI';

import React, { useState, useEffect } from 'react';
import Constant from '../../../../Utils/Constant';
import CustomCard from '../../../../Component/CustomCard';
import { alert } from '../../../../Component/Alert/AlertAction';
import { loader } from '../../../../Component/Loader/LoaderAction';
import { useDispatch, useSelector } from "react-redux";
import request from '../../../../Server/api';
import { Field, reduxForm } from 'redux-form';
import { useHistory } from 'react-router-dom';

import validate from '../../../../Utils/Validation';
import control from '../../../../Component/FormComponent';
import functions from '../../../../Utils/Functions';

const onSubmit = (values) => {

}

const useStyles = UI.makeStyles({
    card: {
        background: 'white',
        height: '90%',
        width: '100%',
        borderRadius: 20,
        alignContent: '',
        alignSelf: ''
    },

    imageUpload: {
        width: '99px',
        height: '99px',
        boxShadow: '0px 3px 6px #00000029',
        opacity: '1',
        backgroundColor: '#11CDEF',
        color: '#FFFFFF',
        borderRadius: '100%',
        cursor: 'pointer',
        textAlign: "center",
    },
    companyLogo: {
        width: '99px',
        height: '99px',
        borderRadius: '100%',
    },
    defaultLogo: {
        paddingTop: '24%'
    },
    inputLabel: {
        color: '#B1B1B1'
    },
    container: {
        padding: '2% 1% 1% 2%'
    },

    rowMargin: {
        marginBottom: '1%'
    },
    btnSubmit: {
        width: '20%',
        background: Constant.colors.btnColor,
        color: '#FFFFFF'
    }
});

let AddUser = (props) => {
    const { handleSubmit, pristine, reset, submitting, invalid } = props

    const classes = useStyles();
    const theme = UI.useTheme();
    const dispatch = useDispatch();
    const history = useHistory()
    const [data, setData] = useState({
        profileUrl: '',
        name: '',
        email: '',
        userTypeId: '5f2927946cbcf64be4fe2fad',
        superUserId: '5f2928456cbcf64be4fe2fb2',
    })


    let loginUserData = null;

    useEffect(() => {

    }, [])


    const onSubmit = (formData) => {
        dispatch(loader(true))
        formData['userTypeId'] = '5f2927946cbcf64be4fe2fad';
        formData['superUserId'] = functions.getLoginUser()._id
        request('SUPER_USER_ADD', formData).then((result) => {
            let reaponce = result.data;
            dispatch(alert({
                status: true,
                message: reaponce.message,
                type: 'success'
            }))
            setData({
                ...data,
                profileUrl: '',
                name: '',
                email: '',
            })
            dispatch(loader(false))
            history.push('/superAdmin/users');
        }).catch(err => {
            if (err.response) {
                let error = err.response.data;
                dispatch(alert({
                    status: true,
                    message: error.message,
                    type: 'error'
                }))
                dispatch(loader(false))
            }
        })
    }


    const selectFile = (event) => {
        document.getElementById('fileId').click();
    }

    const changeFile = (event) => {
        getBase64(event.target.files[0], (base64Image) => {
            setData({ ...data, companyLogo: base64Image })
        })
    }

    const getBase64 = (file, callback) => {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function () {
            callback(reader.result)
        };
        reader.onerror = function (error) {
            console.log('Error: ', error);
        };
    }

    const onInputChange = (event) => {
        setData({ ...data, [event.target.name]: event.target.value })
    }

    return (
        <>
            <form onSubmit={handleSubmit(props => onSubmit(props))}>
                <CustomCard card={classes.card} children={
                    <div className={classes.container}>

                        <UI.Grid
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="flex-start"
                        >
                            <div className={classes.imageUpload}>
                                {
                                    data.companyLogo ? (<img src={data.companyLogo} onClick={selectFile} className={classes.companyLogo} />) : (<img src={Constant.assets.addIcon} onClick={selectFile} className={classes.defaultLogo} />)
                                }
                                <input type="file" id="fileId" name="companyLogo" hidden onChange={changeFile} />
                            </div>
                        </UI.Grid>
                        <UI.Grid
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="flex-start"
                            className={classes.rowMargin}
                        >
                            <Field name="name" component={control.textField} type="text" label="Name" />

                        </UI.Grid>
                        <UI.Grid
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="flex-start"
                            className={classes.rowMargin}
                        >
                            <Field name="email" component={control.textField} type="text" label={Constant.strings.lbl_company_email} />
                        </UI.Grid>

                        <UI.Grid
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="flex-start"
                            className={classes.rowMargin}
                        >
                            <UI.Button className={classes.btnSubmit} variant="contained"
                                style={{ backgroundColor: Constant.colors.btnColor }} type="submit" disabled={invalid || pristine || submitting}>
                                {Constant.strings.submit}
                            </UI.Button>&nbsp;&nbsp;
                            <UI.Button variant="contained" color="secondary" onClick={(e) => {
                                history.push('/superAdmin/users')
                            }}>Cancel</UI.Button>
                        </UI.Grid>
                    </div>
                }
                />
            </form>


        </>
    )
}
AddUser = reduxForm({
    form: 'AddUser',
    onSubmit,
    validate,
    fields: ['name', 'email']
})(AddUser)

export default AddUser;
