import React, { useState, useEffect } from 'react';
import CompanyCard from '../../../../Component/Company/CompanyCard';
import Grid from '@material-ui/core/Grid';
import CustomAddButton from '../../../../Component/CustomAddButton';
import { makeStyles } from '@material-ui/core/styles';
import request from '../../../../Server/api';
import Constant from '../../../../Utils/Constant';
import { alert } from '../../../../Component/Alert/AlertAction';
import { loader } from '../../../../Component/Loader/LoaderAction';
import { useDispatch } from "react-redux";
import { dashboard } from '../SuperAdminAction';
import RecordNotFound from '../../../../Component/RecordNotFound';

const useStyles = makeStyles({
  loadMore: {
    color: '#11CDEF',
    opacity: '1',
    fontSize: '14px',
    marginTop: '3%',
    marginBottom: '3%',
    cursor: 'pointer'
  },
  content: {
    // overflowX : 'scroll'
    // backgroundColor : '#f1f3f9',
    // paddingBottom : '3%'
  }
})

export default function Home(props) {
  console.log('Home props', props);

  const classes = useStyles();
  const dispatch = useDispatch();
  const [data, setData] = useState({
    list: [],
    isData: false,
    totalCount  : 0,
    pageNo : 1
  })


  useEffect(() => {
    loadCompany();
  }, [])

  const loadCompany = () => {
    dispatch(loader(true));
    let postData = {
      pageNo :data.pageNo,
      limit : 10
    }
    request('COMPANY_LIST',postData).then((result) => {
      let responce = result.data;
      if (responce.isSuccess) {
        setData({
          ...data, list: responce.data.companies,
          isData: true,
          totalCount : responce.data.totalCount
        })
      }
      dispatch(loader(false));

    }).catch(err => {
      dispatch(loader(false));
      if (err.response) {
        let error = err.response.data;
        dispatch(alert({ status: true, message: error.message, type: 'error' }))
      }
    })
  }

  const onClick = () => {
    dispatch(dashboard({ label: 'Add Company', isSearch: false }))
    // props.reduxprops.CHANGE_TO_SEARCH({ label: 'Add Company', isSearch: false })
    // props.reduxprops.history.push('/superAdmin/add-company')
    props.history.push('/superAdmin/add-company')
  }

  const loadMore = async () => {
    let newData = [
      {
        _id: 1,
        name: 'Getsocio',
        email: 'Info@getsocio.com',
        desc: '17-July-20',
      },
      {
        _id: 2,
        name: 'Getsocio',
        email: 'Info@getsocio.com',
        desc: '17-July-20',
      },
      {
        _id: 3,
        name: 'Getsocio',
        email: 'Info@getsocio.com',
        desc: '17-July-20',
      },
      {
        _id: 4,
        name: 'Getsocio',
        email: 'Info@getsocio.com',
        desc: '17-July-20',
      }
    ]

    let temp = data.list.concat(newData)
    setData({ ...data, list: temp });
  }
  const handleOption = (value, id) => {
    let postData = {
      id: id
    }
    dispatch(loader(true));
    request('COMPANY_SUSPEND', postData).then((result) => {
      let responce = result.data;
      if (responce.isSuccess) {
        dispatch(loader(false));
        loadCompany();
        dispatch(alert({ status: true, message: responce.message, type: 'success' }))
      }
    }).catch(err => {
      dispatch(loader(false));
      if (err.response) {
        let error = err.response.data;
        dispatch(alert({ status: true, message: error.message, type: 'error' }))
      }
    })
  }


  return (
    <>

      <div className={classes.content}>
        <Grid
          container
          direction="row"
          justify="flex-end"
          alignItems="center"
        >
          <CustomAddButton onClick={onClick.bind(this)} />
        </Grid>

        {
          (data.list.length < 1 && data.isData) ? (<RecordNotFound />) : ''
        }

        {(data.list.length > 0) && <>
          <Grid container spacing={1} >
            {data.list.map((row, index) => (
              <Grid item md={3} key={row._id} spacing={1}>
                <CompanyCard data={row}
                  handleMenuClick={handleOption.bind(this)}
                  backColor={row.isSuspended ? 'sudpend' : 'active'}
                />
              </Grid>
            ))}
          </Grid>

          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
          >
              {
               (data.list.length <  data.totalCount) && <span className={classes.loadMore} onClick={loadMore}> Load More({data.list.length})</span>
              }
            
          </Grid>
        </>
        }
      </div>
    </>
  );
}
