import Constant from '../../../Utils/Constant';
const initialState = {
    isLogin: false,
    searchObj: {
        label: '',
        isSearch: true,
      
    }
};

export default function SuperAdminReducer(state = initialState, action) {
    switch (action.type) {
        case Constant.reducerActions.IS_USER_LOGIN:
            return ({ ...state, isLogin: action.payload })
        case Constant.reducerActions.CHANGE_TO_SEARCH:
            return ({ ...state, searchObj: action.payload })
            default:
            return state
    }
    return state;

} 