import axios from "axios";
import Constant from '../Utils/Constant';
const qs = require('qs');


//     api.interceptors

export const api = axios.create({

});

api.interceptors.request.use((request) => {
    // console.log('inceptor request',request);
    return request;
});

api.interceptors.response.use((response) => {
    // console.log('inceptor response',response.data);
    if (response.data.statusCode == 401) {
        window.location.href = '/login';
    }
    return response;
});

// export default api;

const request = async (url, data = null, param = null) => {
    let queryString = "";

    if (param) {
        queryString = await Object.keys(param).reduce(function (res, v) {
            return res.concat(param[v]);
        }, []);
        queryString = "/" + queryString.join('/');
    }

    let END_POINT = await Constant.URL.find(e => e.name === url);
    if (!END_POINT) {
        console.log('End point is not registered');
        return
    }

    let reduxtoken = '';
    if (localStorage.getItem('@AsyncStorage:persist:root'))
        reduxtoken = (JSON.parse(JSON.parse(localStorage.getItem('@AsyncStorage:persist:root')).loginReducer)).userToken
    // alert("global token---"+Constant.global.token)

    let config = {
        url: END_POINT.url + queryString,
        baseURL: 'https://emarket-production.herokuapp.com/api/v1/',
        method: END_POINT.method,
        headers: {
            authorization: "Bearer " + (Constant.global.token !== '' ? Constant.global.token : reduxtoken !== '' ? reduxtoken : '')
        },
        params: null,
        // paramsSerializer: function (params) {
        //     return qs.stringify(params, { arrayFormat: 'brackets' })
        // },
        data: data,
        responseType: 'json'
    }
    
    // alert(JSON.stringify(config))
    return api.request(config)
}

export default request;

