import React, { useState } from 'react';
import './LoginPage.css';
import Constant from '../../Utils/Constant';
import CustomButton from '../../Component/CustomButton';
import { validateInput } from 'validateinput';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import CustomCard from '../../Component/CustomCard';
import { Typography, Alert } from '@material-ui/core';
import CustomTextField from '../../Component/CustomTextField';
import { alert } from '../../Component/Alert/AlertAction';
import { loader } from '../../Component/Loader/LoaderAction';
import { useDispatch, useSelector } from "react-redux";


import request from '../../Server/api';
export default function LoginPage(props) {
    const classes = useStyles();
    const theme = useTheme();
    const [email, setEmail] = useState('');

    const [password, setPassword] = useState('');
    const dispatch = useDispatch();

    const [loading, setLoading] = useState(false);

    const [error, setError] = useState('');

    const onClick = async (userName, password) => {
        const type = validateInput(userName, password)
        if (type === Constant.strings.EMPTY_ERROR) {
            setError(Constant.strings.emailpasserror)
        } else {
            if (type === Constant.strings.EMAIL_ERROR) {
                setError(Constant.strings.empty_email)
            }
            if (type === Constant.strings.INVALID_EMAIL) {
                setError(Constant.strings.invalid_email)
            }
            if (type == Constant.strings.PASS_ERROR) {
                setError(Constant.strings.shortPassword)
            }
            if (type === Constant.strings.VALIDATE) {
                const Input = { "email": userName, "password": password }
                dispatch(loader(true))
                request('LOGIN', Input).then(async (result) => {
                    const tokenData = result.data;
                    await props.SAVE_USER_TOKEN(tokenData.data)
                    Constant.global.token = result.data.data
                    request('LOGGED_IN_USER').then(async (userresult) => {
                        dispatch(loader(false));
                        
                        const responseData = userresult.data
                        dispatch(alert({
                            status: true,
                            message: userresult.data.message,
                            type: 'success'
                        }))
                        props.IS_USER_LOGIN(true);
                        props.SAVE_USER_DATA(responseData.data)
                        Constant.global.token =''
                        props.history.push('./superAdmin/home')
                    }).catch(err => {
                        let error = err.response.data;
                        dispatch(alert({
                            status: true,
                            message: error.message,
                            type: 'error'
                        }))
                        dispatch(loader(false))
                    }
                    )
                }).catch(err => {
                    let error = err.response.data;
                    dispatch(alert({
                        status: true,
                        message: error.message,
                        type: 'error'
                    }))
                    dispatch(loader(false))
                })

            }
        }

    }


    return (
        <div className={classes.root}>

            <CustomCard card={classes.card} children={<div className={classes.mainContainer}>
                <div className={classes.leftContainer}>
                    <img src={Constant.assets.rapideraLogo} className={classes.logo} />
                    <div className={classes.formcontainer}>
                        <CustomTextField id='email' onChange={(txt) => { setEmail(txt.target.value) }} label={Constant.strings.lbl_email} type='email' />
                        <CustomTextField id='password' onChange={(txt) => { setPassword(txt.target.value) }} label={Constant.strings.lbl_password} type='password' />
                        <div style={{ margin: Constant.dimensions.small20 }}>
                            <Typography variant="caption" className={classes.error}>{error}</Typography>
                        </div>
                        {/* <Button style={{width: 200, height: 50}}  >Login
                            <CircularProgress />
                        </Button> */}
                        <CustomButton isLoading={loading} title={Constant.strings.login} onClick={onClick.bind(this, email, password)} ></CustomButton>
                        <Typography variant="caption" className={classes.copyRight}>{Constant.strings.copyrighttext}</Typography>
                    </div>
                </div>
                <div className={classes.rightContainer}>
                    <img src={Constant.assets.intro_banner} />
                </div>
            </div>} />
        </div>
    );
}


const useStyles = makeStyles({
    root: {
        background: Constant.colors.mainScreenColor,
        height: '100vh',
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
        display: 'flex',
    },
    card: {
        background: 'white',
        height: '90%',
        width: '80%',
        borderRadius: 20,
        alignContent: 'center',
        alignSelf: 'center'
    },
    rightContainer: {
        height: '100vh', width: '40%'
    },
    leftContainer: {
        height: '100vh', width: '60%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        margin: Constant.dimensions.medium50

    },
    formcontainer: {
        width: '70%',
        alignItems: 'center',
        marginTop: Constant.dimensions.medium40
    },
    logo: {
        height: '15%',
    },
    copyRight: {
        color: Constant.colors.copyrightcolor,
        position: 'absolute',
        bottom: Constant.dimensions.medium50
    },
    error: {
        color: Constant.colors.redCardColor
    },
    mainContainer: {
        flexDirection: 'row', display: 'flex'
    }

});

