import React, { useState, useEffect } from 'react';
import UserCard from '../../../../Component/User/UserCard';
import Grid from '@material-ui/core/Grid';
import UserStyle from './UserStyle.scss';
import CustomAddButton from '../../../../Component/CustomAddButton';
import { makeStyles } from '@material-ui/core/styles';
import { dashboard } from '../SuperAdminAction';
import { useDispatch, useSelector } from "react-redux";
import request from '../../../../Server/api'
import { loader } from '../../../../Component/Loader/LoaderAction';
import { alert } from '../../../../Component/Alert/AlertAction';
import RecordNotFound from '../../../../Component/RecordNotFound';

export default function UserList(props) {
    const dispatch = useDispatch();
    const [data, setUsersData] = useState({
        list: [],
        isData: false,
        totalCount  : 0
    });

    useEffect(() => {
        loadList()
    }, []);

    const loadList = () => {
        dispatch(loader(true));
        let postData = {
            pageNo :1,
            limit : 10
          }
        request('SUPER_USER_LIST',postData).then((result) => {
            let apiResponce = result.data;
            setUsersData({
                ...data, list: apiResponce.data.superUsers,
                totalCount : apiResponce.data.totalCount,
                isData: true,
            })
            dispatch(loader(false));
        }).catch(err => {
            if (err.response) {
                dispatch(loader(false));
                let error = err.response.data;
                dispatch(alert({
                    status: true,
                    message: error.message,
                    type: 'error'
                }))
            }
        })
    }

    const onClick = () => {
        dispatch(dashboard({ label: 'Add User', isSearch: false }))
        props.history.push('/superAdmin/add-user')
    }

    return (
        <div>
            <Grid
                container
                direction="row"
                justify="flex-end"
                alignItems="center"
            >
                <CustomAddButton onClick={onClick.bind(this)} />

                {
                    (data.list.length < 1 && data.isData) ? (<RecordNotFound />) : ''
                }
            </Grid>
            {
                (data.list.length > 0) ? (<Grid container spacing={1}>
                    {data.list.map((row, index) => (
                        <Grid item md={3} key={row._id}>
                            <UserCard data={row} />
                        </Grid>
                    ))}
                </Grid>) : ''
            }
        </div>
    )
}
