import { connect } from "react-redux";
import SuperAdminHome from "./SuperAdminHome";

let mapstatetoprop = (state) => {
    return {
        isLogin: state.loginReducer.isLogin,
        searchObj: state.SuperAdminReducer.searchObj,
        userToken: state.loginReducer.userToken,
        userData: state.loginReducer.userData

    }
};
let mapdispatchtoprop = (dispatch) => {
    return {
        IS_USER_LOGIN: (data) => dispatch({ type: 'IS_USER_LOGIN', payload: data }),
        CHANGE_TO_SEARCH: (data) => dispatch({ type: 'CHANGE_TO_SEARCH', payload: data }),
        SAVE_USER_DATA: (data) => dispatch({ type: 'SAVE_USER_DATA', payload: data }),
        SAVE_USER_TOKEN: (data) => dispatch({ type: 'SAVE_USER_TOKEN', payload: data }),

    }
};
export default connect(mapstatetoprop, mapdispatchtoprop)(SuperAdminHome);