import React from 'react';
import Constant from '../Utils/Constant'
import { Paper, Grid, TextField, FormControlLabel, Checkbox, Button, Typography } from '@material-ui/core';

export default function CustomTextField(props) {
    return (
        <Grid container spacing={8} alignItems="flex-end">
            <Grid item md={true} sm={true} xs={true}>
                <TextField
                    style={{ fontSize: Constant.dimensions.small12, color: Constant.colors.btnColor }}
                    id={props.email}
                    size='small'
                    onChange={props.onChange}
                    label={props.label}
                    type={props.type} fullWidth  required 
                    name ={props.name}
                    value = {props.value}/>
            </Grid>
        </Grid>
    );

}