import { connect } from "react-redux";
import LoginPage from "./LoginPage";
import Constant from '../../Utils/Constant'

let mapstatetoprop = (state) => {
    return {
        isLogin: state.loginReducer.isLogin,
        userData: state.loginReducer.userData,
        userToken: state.loginReducer.userToken
    }
};
let mapdispatchtoprop = (dispatch) => {
    return {
        IS_USER_LOGIN: (data) => dispatch({ type: Constant.reducerActions.IS_USER_LOGIN, payload: data }),
        SAVE_USER_DATA: (data) => dispatch({type: Constant.reducerActions.SAVE_USER_DATA, payload: data }),
        SAVE_USER_TOKEN: (data) => dispatch({type: Constant.reducerActions.SAVE_USER_TOKEN, payload: data })

    }
};
export default connect(mapstatetoprop, mapdispatchtoprop)(LoginPage);