import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Login from './Pages/Login';
import SuperAdminHome from './Pages/Dashboard/SuperAdmin/'
import Intro from './Pages/Intro/Intro';
import { connect } from "react-redux";
import PageNotFound from './Component/PageNotFound';

class AppNavigation extends React.Component {
  
    render() {
        return (
            <div>
                <BrowserRouter>
                    <Switch>
                        <Route path="/login" component={Login} />
                        <Route path="/" exact component={!this.props.isLogin ? Intro : SuperAdminHome} />
                        <Route path="/superAdmin" component={SuperAdminHome} /> 
                        <Route component = {PageNotFound}/>
                    </Switch>
                </BrowserRouter>
            </div>
        );
    }
}

let mapstatetoprop = (state) => {
    return {
        isLogin: state.loginReducer.isLogin,

    }
};
let mapdispatchtoprop = (dispatch) => {
    return {
        IS_USER_LOGIN: (data) => dispatch({ type: 'IS_USER_LOGIN', payload: data }),

    }
};


export default connect(mapstatetoprop, mapdispatchtoprop)(AppNavigation);