import { dashboard } from './SuperAdminAction';
import PageNotFound from '../../../Component/PageNotFound';
import request from '../../../Server/api';
import { alert } from '../../../Component/Alert/AlertAction';
import { loader } from '../../../Component/Loader/LoaderAction';
import ProductDetail from './Products/ProductDetails/ProductDetail';
import {Route,Switch,Link,useHistory} from "react-router-dom";

import Home from '../SuperAdmin/Home/Home';
import Product from '../SuperAdmin/Products/Product';
import Setting from '../SuperAdmin/Setting';
import UserList from '../SuperAdmin/user/UserList';
import AddCompany from './Home/AddCompany';
import AddUser from './user/AddUser';

const drawerWidth = 240;

const useStyles = M.makeStyles((theme) => ({
    root: {
        display: 'flex',
        fontFamily: 'Poppins'
    },
    appBar: {
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),

        color: '#FFFFFF',
        backgroundColor: '#f6cee6'
    },
    appBarShift: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth,
        transition: theme.transitions.create(['margin', 'width'], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        width: drawerWidth,
    },
    drawerHeader: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
        justifyContent: 'flex-end',
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(1),
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        marginLeft: -drawerWidth,
        backgroundColor: '#f1f3f9',
        // height: theme.spacing(100),
        height: '100vh',

    },
    contentShift: {
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        marginLeft: 0,
    },
    large: {
        width: theme.spacing(5),
        height: theme.spacing(5),
    },
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: M.fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: M.fade(theme.palette.common.white, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(3),
            width: 'auto',
        },
    },
    searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: 'black',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '20ch',
        },
        backgroundColor: "#FFFFFF",
        borderRadius: '10px',
        color: 'black',
        zIndex: '-1'
    },
    logoImage: {
        width: '170px',
        height: '43px'
    },
    dashboardHeading: {
        color: '#404040',
        opacity: '1',
        fontSize: '30px'
    },
    iconColor: {
        color: '#FFFFFF'
    },
    footer : {

    }
}));

const SuperAdminHome = (props) => {
    const { location, searchObj } = props;
    const dispatch = useDispatch();
    const dashboardData = useSelector((state) => state.SuperAdminReducer).searchObj;

    const classes = useStyles();
    const theme = M.useTheme();
    const [open, setOpen] = useState(true);
    let url = (location.pathname).split('/');
    let redirect = null;
    if (!url[2]) {
        redirect = "/superAdmin/home"
    } else {
        redirect = '/' + url[1] + '/' + url[2];
    }

    // console.log('url', '/' + url[1] + '/' + url[2]);

    const [menu, setmenu] = useState({
        selectedMenu: redirect
    });


    const history = useHistory();

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    const navigate = (e, route) => {
        dispatch(dashboard({ label: '', isSearch: true }))
        // props.CHANGE_TO_SEARCH({ label: '', isSearch: true })
        setmenu({ ...menu, selectedMenu: route })
        history.push(route);
    }

    const logout = () => {
        dispatch(loader(true))
        request('LOGOUT', { 'token': props.userToken }).then(async (userresult) => {
            dispatch(alert({
                status: true,
                message: userresult.data.message,
                type: 'success'
            }));
            dispatch(loader(false));
            props.IS_USER_LOGIN(false);
            await props.SAVE_USER_DATA({})
            await props.SAVE_USER_TOKEN('')
            history.push('/login');
        }).catch(err => {
            let error = err.response.data;
            dispatch(alert({
                status: true,
                message: error.message,
                type: 'error'
            }))
            dispatch(loader(false))
        }
        )

    }

    const notification = () => {

    }

    const onClose = () => {
        console.log('onclose');

    }

    // useEffect(()=>{
    //         console.log('super admin hook called');
    // },[])

    const leftMenu = [
        {
            name: 'Home',
            icon: <M.HomeOutlinedIcon style={{ color: Constant.colors.blue }} />,
            url: "/superAdmin/home",
            color: "",
            urls: ['/superAdmin/home', '/superAdmin/add-company']
        },
        {
            name: 'Products',
            icon: <M.PictureInPictureIcon style={{ color: Constant.colors.btnColor }} />,
            url: "/superAdmin/product",
            color: "",
            urls: ['/superAdmin/product', '/superAdmin/add-product', 'superAdmin/product-detail']
        },
        {
            name: 'Users',
            icon: <M.PersonOutlineIcon style={{ color: Constant.colors.orange }} />,
            url: "/superAdmin/users",
            color: "",
            urls: ['/superAdmin/users', '/superAdmin/add-users']
        },
        {
            name: 'Setting',
            icon: <M.SettingsOutlinedIcon style={{ color: Constant.colors.btnColor }} />,
            url: "/superAdmin/setting",
            color: "",
            urls: ['/superAdmin/setting']
        }
    ]

    return (
        <div className={classes.root}>
            <M.CssBaseline />
            <M.AppBar
                position="fixed"
                className={clsx(classes.appBar, {
                    [classes.appBarShift]: open,
                })}
            >
                <M.Toolbar>
                    <M.IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        edge="start"
                        className={clsx(classes.menuButton, open && classes.hide)}
                    >
                        <M.MenuIcon />
                    </M.IconButton>

                    <M.Grid container
                        direction="row"
                        justify="flex-start"
                        alignItems="center">

                        {/* <div className='searchBox'>
                    <M.SearchIcon />
                    </div> */}

                        {
                            (dashboardData.isSearch) ? (<div className={classes.search}>
                                <div className={classes.searchIcon}>
                                    <M.SearchIcon />
                                </div>
                                <M.InputBase
                                    placeholder="Search"
                                    classes={{
                                        root: classes.inputRoot,
                                        input: classes.inputInput,
                                    }}
                                    inputProps={{ 'aria-label': 'search' }}
                                />
                            </div>) : (<span className={classes.dashboardHeading}>
                                {dashboardData.label}
                            </span>)
                        }
                 </M.Grid>

                    <M.Grid
                        container
                        direction="row"
                        justify="flex-end"
                        alignItems="center"
                    >
                        <M.Tooltip title="Notification" onClick={notification}>
                            <M.IconButton aria-label="Notification">
                                <M.NotificationsNoneIcon className={classes.iconColor} />
                            </M.IconButton>
                        </M.Tooltip><M.Avatar alt="Remy Sharp" src={props.userData.profileUrl} className={classes.large} /> &nbsp;&nbsp; &nbsp;&nbsp;
                            {props.userData.name}
                        <M.Tooltip title="Logout" onClick={logout}>
                            <M.IconButton aria-label="Logout">
                                <M.ExpandMoreIcon className={classes.iconColor} />
                            </M.IconButton>
                        </M.Tooltip>
                    </M.Grid>
                </M.Toolbar>
            </M.AppBar>
            <M.Drawer
                className={classes.drawer}
                variant="persistent"
                anchor="left"
                open={open}
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <div className={classes.drawerHeader}>
                    <M.Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center">
                        <img src={rapideraLogo} className={classes.logoImage} />
                    </M.Grid>
                    <M.IconButton onClick={handleDrawerClose}>
                        {theme.direction === 'ltr' ? <M.ChevronLeftIcon /> : <M.ChevronRightIcon />}
                    </M.IconButton>
                </div>
                <M.List>
                    {leftMenu.map((item, index) => (
                        <div primary={item.name} className={(item.urls.includes(menu.selectedMenu)) ? 'activeMenu' : ''} key={item.name}>
                            <M.ListItem button key={item.name} onClick={(e) => navigate(e, item.url)} >
                                <M.ListItemIcon >{item.icon}</M.ListItemIcon>
                                <M.ListItemText  >{item.name}</M.ListItemText>
                            </M.ListItem>
                        </div>

                    ))}
                </M.List>
                {/* <Divider /> */}
            </M.Drawer>
            <main
                className={clsx(classes.content, {
                    [classes.contentShift]: open,
                })}
            >
                <div className={classes.drawerHeader} />
                <Switch>
                    <Route path="/superAdmin/product" component={Product} />
                    <Route path="/superAdmin/product-detail" component={ProductDetail} />
                    <Route path="/superAdmin/users" component={UserList} />
                    <Route path="/superAdmin/setting" component={Setting} />
                    <Route path="/superAdmin/add-company/:companyId?" component={AddCompany} />
                    <Route path="/superAdmin/add-user" component={AddUser} />
                    <Route path="/superAdmin/home" component={Home} />
                    <Route path="/" component={Home} />
                    <Route component={PageNotFound} />
                </Switch>
                
            </main>
        </div>
    );
}

export default SuperAdminHome;


